import axios from 'axios';
import { isLoggedIn } from '../auth';

/**
 * Fetches the all jobs
 * @param {Number} limit - the query limit.
 * @param {Number} offset - the query offset.
 * @returns {Promise} jobs - a promise of jobs to be resolved.
 */
export function getJobs(limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/jobs?limit=${limit}&offset=${offset}`).then((response) => {
			return response;
		});
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches a single job object
* @param {String} id - job Id
* @returns {Promise} response - a promise of a single job
*/
export function getJob(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/jobs/${id}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Delete a single job object
* @param {String} id - job Id
* @returns {Promise} response - a promise of a single job to be deleted
*/
export function deleteJob(id) {
	if (isLoggedIn()) {
		return axios.delete(`/v1/jobs/${id}`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {  
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
 * Edits a job
 * @param {Object} jobData - Object of the job information.
 * @returns {Promise} job - a promise of job to be resolved.
 */
export function updateJob(jobData) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/jobs/${jobData.id}`, jobData);
	}
	return Promise.resolve(new Response(
		{ message: 'Unauthorised' },
		{ status: 403, statusText: 'Unauthorised' }
	));
}

/**
 * Create a new job
 * @param {Object} userData - Object of the job information.
 * @returns {Promise} jobs - a promise of job to be resolved.
 */
export function createJob(jobData) {
	if (isLoggedIn()) {
		return axios.post('/v1/jobs', jobData);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
 * Fetches the all jobs
 * @param {Number} limit - the query limit.
 * @param {Number} offset - the query offset.
 * @returns {Promise} jobs - a promise of jobs to be resolved.
 */
export function getApplicants(id, limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/jobs/${id}/applicants?limit=${limit}&offset=${offset}`).then((response) => {
			return response;
		});
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
 * Change the status of a job applicant
 * @param {Number} id - Job id
 * @param {Object} data - Object of the status information.
 * @returns {Promise} job - a promise of job to be resolved.
 */
export function changeStatus(id, data) {
	if (isLoggedIn()) {
		return axios.patch(`/v1/jobs/${id}/status`, data);
	}
	return Promise.resolve(new Response(
		{ message: 'Unauthorised' },
		{ status: 403, statusText: 'Unauthorised' }
	));
}

export function sendEmail(data) {
	if (isLoggedIn()) {
		return axios.post('/v1/jobs/email' , data);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}

/**
* Fetches list of jobs belonging to an employer
* @param {String} id - Employer ID
* @returns {Promise} response - a promise of a single job
*/
export function getEmployerJobs(id) {
	if (isLoggedIn()) {
		return axios.get(`/v1/jobs/${id}/employer`);
	}

	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}