</template>

<script>
import { createUser, getStates, getCities } from '../../utils/api/users';
import user from '../../utils/validations/user';
import eventHub from '../../utils/eventhub';
import form from '../../mixins/form';
import { success, fail } from '../../utils/toast';

export default {
	name: 'AddUser',
	mixins: [form],
	data() {
		return {
			itemData: {
				fname: '',
				lname: '',
				email: '',
				phone: '',
				password: ''
			},
			errors: {},
			states: [],
			statesLoaded: false,
			loadingStates: true,
			city_id: null,
			cities: [],
			citiesLoaded: false,
			loadingCities: true,
			state_id: null
		};
	},
	methods: {
		/**
		 * This creates a new user
		 */
		createUser() {
			/**
			 * Validate input
			 */
			this.itemData.state_id = this.state_id;
			this.itemData.city_id = this.city_id;
			this.validate(this.itemData, user.create);
			if (Object.keys(this.issues).length > 0) {
				this.errors = this.issues;
				return false;
			}

			createUser(this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.clearFormField();
						/**
						 * Bubble event back up for reactivity
						 */
						eventHub.$emit('user-created', response.data);
						success({
							title: 'Success! New user has been added'
						});
					}
					this.loading = false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Ooops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		},
		getStates() {
			getStates()
				.then((response) => {
					if (response.status === 200) {
						this.states = response.data.data;
						this.statesLoaded = true;
						this.loadingStates = false;
					}
				})
				.catch((error) => {
					this.statesLoaded = false;
					this.loadingStates = false;
					return error;
				});
			return true;
		},
		getCities(id) {
			getCities(id)
				.then((response) => {
					if (response.status === 200) {
						this.cities = response.data.data;
						this.citiesLoaded = true;
						this.loadingCities = false;
					}
				})
				.catch((error) => {
					this.statesLoaded = false;
					this.loadingCities = false;
					return error;
				});
			return true;
		}
	},
	created() {
		this.getStates();
	},
	watch: {
		state_id() {
			this.getCities(this.state_id);
		}
	}
};
</script>
