<template>
  <div>
    <template v-if="usersLoading">
      <div class="div__loading">
        <Loader />
      </div>
    </template>
    <template v-else>
      <div class="main__header">
        <div class="main__primary">
          <h3 class="main__title">Users</h3>

          <div class="form-group form-group--flex">
            <label class="label" for="career">
              <select class="custom-select form-control" id="career" v-model="selectedCareer">
                <option value selected>Select Career</option>
                <template v-if="careersLoaded">
                  <option
                    v-for="career in careers"
                    :key="career.id"
                    :value="career.id"
                  >{{career.name}}</option>
                </template>
                <template v-else>
                  <option disabled>No career available</option>
                </template>
              </select>
            </label>
            <label class="label" for="state">
              <select class="custom-select form-control" id="state" v-model="selectedState">
                <option value selected>Select State</option>
                <template v-if="statesLoaded">
                  <option v-for="state in states" :key="state.id" :value="state.id">{{state.name}}</option>
                </template>
                <template v-else>
                  <option disabled>No state available</option>
                </template>
              </select>
            </label>
            <label class="label" for="city">
              <select class="custom-select form-control" id="city" v-model="selectedCity">
                <option value selected>Select LGA</option>
                <template v-if="citiesLoaded">
                  <option v-for="city in cities" :key="city.id" :value="city.id">{{city.name}}</option>
                </template>
                <template v-else>
                  <option disabled>No LGA available</option>
                </template>
              </select>
            </label>
            <div class="filter-loader">
              <Loader v-if="loadingFilters" />
            </div>
            <button
              @click="filterUsers"
              :disabled="!(this.selectedCareer || this.selectedCity || this.selectedState)"
              class="btn btn__outline"
            >Filter</button>
            <button
              class="btn btn__primary ml-10"
              type="button"
              name="button"
              data-toggle="modal"
              data-target="#emailModal"
              :disabled="!selectedUsers.length || !filteredIds.length"
            >Send Email</button>
          </div>
        </div>
        <div class="d-flex">
          <!-- remove v-if to show employer recommendation -->
          &nbsp; &nbsp;
          <div class="form-group">
            <label class="label" for="category">
              <Loader v-if="loading" />
              <select
                class="custom-select form-control"
                id="category"
                v-model="selectedEmployer"
                :class="[{'is-invalid': loadingEmployers}]"
              >
                <option value select>Select Employer</option>
                <template v-if="employersLoaded">
                  <option
                    v-for="employer in employers"
                    :key="employer.id"
                    :value="employer.id"
                  >{{employer.business_name}}</option>
                </template>
                <template v-else>
                  <option disabled>No employer available</option>
                </template>
              </select>
              <button
                :disabled="!selectedUsers.length || !selectedEmployer"
                class="btn btn__outline"
                @click="recommend"
              >Recommend</button>
            </label>
          </div>
          <button
            v-if="getLoggedUser.category === 'administrator'"
            class="btn btn__primary"
            type="button"
            name="button"
            data-toggle="modal"
            data-target="#userModal"
          >
            <span class="btn__icon">
              <svg width="24" height="13" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path
                  d="M8 5H5V2H3v3H0v2h3v3h2V7h3V5zm10 1c1.66 0 2.99-1.34 2.99-3S19.66 0 18 0c-.32 0-.63.05-.91.14.57.81.9 1.79.9 2.86 0 1.07-.34 2.04-.9 2.86.28.09.59.14.91.14zm-5 0c1.66 0 2.99-1.34 2.99-3S14.66 0 13 0c-1.66 0-3 1.34-3 3s1.34 3 3 3zm6.62 2.16C20.45 8.89 21 9.82 21 11v2h3v-2c0-1.54-2.37-2.49-4.38-2.84zM13 8c-2 0-6 1-6 3v2h12v-2c0-2-4-3-6-3z"
                  fill="#fff"
                />
              </svg>
            </span>Add User
          </button>
        </div>
      </div>
      <section class="main-grid__row contacts contacts--list">
        <div class="contacts--list__wrapper">
          <div class="contacts--list__item head">
            <div></div>
            <p>Basic info</p>
            <p>Status</p>
            <p>Category</p>
            <p>phone</p>
            <p>Email</p>
            <p>Actions</p>
          </div>
          <div class="contacts__body">
            <template v-if="usersLoaded && users.length > 0">
              <UserItem v-for="user in users" :key="user.id" :user-data="user">&nbsp;</UserItem>
            </template>
            <template v-else-if="usersLoaded">
              <p>No user found</p>
            </template>
            <template v-else>
              <p>An error occurred while loading users, please try again.</p>
            </template>
          </div>
        </div>
      </section>
    </template>
    <Pagination :options="{ limit, offset, total, baseUrl }" v-if="total > limit" />
    <AddUser />
    <SendEmail :emails="this.filteredIds" />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import {
  getUsers,
  recommendUsers,
  getStates,
  getCities,
  filterUsers
} from "../../utils/api/users";
import getEmployers from "../../utils/api/employer";
import { getCareers } from "../../utils/api/career";
import Loader from "../../components/Loader";
import UserItem from "./UserItem";
import Pagination from "../../components/Pagination";
import eventHub from "../../utils/eventhub";
import AddUser from "./AddUser";
import { success, fail } from "../../utils/toast";
import SendEmail from "./SendEmail";

export default {
  name: "Users",
  data() {
    return {
      users: [],
      usersLoading: true,
      usersLoaded: false,
      employers: [],
      employersLoaded: false,
      loadingEmployers: false,
      selectedEmployer: "",
      limit: 800,
      offset: 0,
      total: 0,
      baseUrl: "users",
      selectedUsers: [],
      loading: false,
      careers: [],
      states: [],
      cities: [],
      citiesLoaded: false,
      statesLoaded: false,
      careersLoaded: false,
      selectedState: "",
      selectedCity: "",
      selectedCareer: "",
      loadingFilters: false,
      filteredIds: []
    };
  },
  mounted() {
    this.getEmployers();
    this.getStates();
    this.getCareers();
  },
  created() {
    this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
    this.offset = this.$route.query.offset
      ? this.$route.query.offset
      : this.offset;
    this.getUsers();
    /**
     * React and refresh list of users
     */
    eventHub.$on("user-created", user => {
      this.users.unshift(user);
    });
    eventHub.$on("select-user", payload => {
      this.selectedUsers.push({ employee_id: payload });
      this.filteredIds.push(payload);
    });
    eventHub.$on("deselect-user", payload => {
      const index = this.selectedUsers.find(
        item => item.employee_id === payload
      );
      this.selectedUsers.splice(this.selectedUsers.indexOf(index), 1);
    });
  },
  computed: {
    ...mapGetters(["getLoggedUser"])
  },
  methods: {
    getUsers() {
      this.usersLoading = true;
      getUsers(this.limit, this.offset)
        .then(response => {
          if (response.status === 200) {
            this.users = response.data.data;
            this.total = response.data.total;
            this.usersLoaded = true;
            return true;
          }
          this.usersLoaded = false;
          return false;
        })
        .catch(error => {
          this.usersLoaded = false;
          return error;
        })
        .finally(() => {
          this.usersLoading = false;
        });
      return true;
    },
    getEmployers() {
      this.loadingEmployers = true;
      getEmployers(this.limit, this.offset)
        .then(response => {
          if (response.status === 200) {
            this.employers = response.data.data;
            this.employersLoaded = true;
            return true;
          }
          this.employersLoaded = false;
          return false;
        })
        .catch(error => {
          this.employersLoaded = false;
          return error;
        })
        .finally(() => {
          this.loadingEmployers = false;
        });
      return true;
    },
    recommend() {
      this.loading = true;
      recommendUsers({
        employer_id: this.selectedEmployer,
        employees: this.selectedUsers
      })
        .then(response => {
          if (response.status === 201) {
            success({
              title: "Success! Users have been recommended"
            });
            this.loading = false;
            eventHub.$emit("rerender");
          }
          return false;
        })
        .catch(error => {
          this.loading = false;
          fail({
            title: "Oops! Something went wrong",
            text: error.data
          });
          return false;
        });
      return true;
    },
    filterUsers() {
      let query = "";
      if (this.selectedCity) {
        query = query.concat(`city=${this.selectedCity}&`);
      }
      if (this.selectedState) {
        query = query.concat(`state=${this.selectedState}&`);
      }
      if (this.selectedCareer) {
        query = query.concat(`career=${this.selectedCareer}`);
      }
      filterUsers(query)
        .then(response => {
          if (response.status === 200) {
            this.users = response.data.data;
            this.total = response.data.total;
            this.filteredIds = Object.keys(this.users).map(
              item => this.users[item].id
            );
            this.usersLoaded = true;
            return true;
          }
          this.usersLoaded = false;
          return false;
        })
        .catch(error => {
          this.usersLoaded = false;
          return error;
        })
        .finally(() => {
          this.usersLoading = false;
        });
      return true;
    },
    getStates() {
      this.loadingFilters = true;
      getStates()
        .then(response => {
          if (response.status === 200) {
            this.states = response.data.data;
            this.statesLoaded = true;
            this.loadingFilters = false;
            return true;
          }
          this.statesLoaded = false;
          return false;
        })
        .catch(error => {
          this.loadingfilters = false;
          return error;
        });
      return true;
    },
    getCities() {
      this.loadingFilters = true;
      getCities(this.selectedState)
        .then(response => {
          if (response.status === 200) {
            this.cities = response.data.data;
            this.citiesLoaded = true;
            this.loadingFilters = false;
            return true;
          }
          this.citiesLoaded = false;
          return false;
        })
        .catch(error => {
          this.loadingFilters = false;
          return error;
        });
      return true;
    },
    getCareers() {
      this.loadingFilters = true;
      getCareers()
        .then(response => {
          if (response.status === 200) {
            this.careers = response.data.data;
            this.loadingFilters = false;
            this.careersLoaded = true;
            return true;
          }
          this.careersLoaded = false;
          return true;
        })
        .catch(error => {
          this.loadingFilters = false;
          return error;
        });
      return false;
    }
  },
  watch: {
    $route() {
      this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
      this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
      this.getUsers();
    },
    selectedState() {
      this.getCities();
    }
  },
  components: {
    UserItem,
    Loader,
    Pagination,
    AddUser,
    SendEmail
  }
};
</script>

