<template>
	<div>
		<div class="contacts--list__item">
			<label class="check">
				<input v-model="selected" :value="user.id" type="checkbox">
				<div class="check-mark"></div>
			</label>
			<div class="contacts__info"
				:key="user.id">
				<div class="contacts__img">
					<img
						v-if="user.avatar"
						:src="user.avatar"
						alt="Profile picture">
					<img v-else src="../../assets/img/profile.png" alt="Profile picture">
				</div>
				<div class="info__primary">
					<p class="info__name">{{ fullName }}</p>
					<p class="info__email">{{ this.user.email }}</p>
				</div>
			</div>
			<p>
				<span :class="isActive">
                    {{ user.active === 1 ? 'Active' : 'Suspended'}}
				</span>
			</p>
			<template v-if="user.acl">
				<p>{{ user.acl.name }}</p>
			</template>
			<template v-else>
				<p></p>
			</template>
			<p>{{ user.phone }}</p>
			<p class="info__address">{{ this.user.email }}</p>
			<p>
				<router-link
					:to="{ name: 'user-profile',
					params: { userData: userData, id: user.id }}"
					class="btn btn__primary">
					View Profile
				</router-link>
			</p>
		</div>
	</div>
</template>

<script>
import eventHub from '../../utils/eventhub';

export default {
	name: 'UserItem',
	props: {
		userData: {
			type: Object,
			default() {
				return {
					id: null,
					fname: '',
					lname: '',
					email: '',
					phone: ''
				};
			}
		}
	},
	data() {
		return {
			user: this.userData,
			selected: false
		};
	},
	watch: {
		selected(val) {
			if (val) {
				this.selectUser();
			} else this.deselectUser();
		}
	},
	methods: {
		selectUser() {
			eventHub.$emit('select-user', this.user.id);
		},
		deselectUser() {
			eventHub.$emit('deselect-user', this.user.id);
		}
	},
	filters: {
		date(value) {
			return moment(value).format('Do, MMM YYYY');
		}
	},
	computed: {
		fullName() {
			return `${this.user.fname} ${this.user.lname}`;
		},
		isActive() {
			return [
				this.user.active === 1
					? 'badge badge-pill badge-success'
					: 'badge badge-pill badge-danger'
			];
		}
	}
};
</script>

<style scoped>
	.badge {
		font-weight: normal;
	}
	.contacts--list__item {
		display: grid;
		grid-template-columns: 35px 2fr 80px repeat(2, 150px) 2fr 150px!important;
	}
</style>
