<template>
  <div
    id="emailModal"
    aria-labelledby="emailModalTitle"
    class="modal fade"
    tabindex="-1"
    role="dialog"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg" role="document">
      <div class="modal-content">
        <div class="modal-header modal__header">
          <h3 class="modal-title" id="emailModalTitle">Send Email</h3>
          <button
            @click.prevent="clearErrors()"
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>

        <div class="modal-body modal__body">
          <form class="modal__form" method="post" @submit.prevent="sendEmail">
            <div class="form-row">
              <div class="form-group col-md-12">
                <label class="modal__label" for="subject">Subject</label>
                <input
                  :class="[{'is-invalid': errors.subject}]"
                  @keydown="clearErrors($event.target.name)"
                  v-model="itemData.subject"
                  value
                  name="subject"
                  type="text"
                  class="form-control"
                  id="subject"
                  placeholder="Subject"
                />
                <span class="modal__form__icon">
                  <svg width="24" height="24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <title>Person Icon</title>
                    <path fill="none" d="M0 0h24v24H0z" />
                    <path fill="none" d="M4 4h16v16H4z" />
                    <path
                      d="M12 12c2.21 0 4-1.79 4-4s-1.79-4-4-4-4 1.79-4 4 1.79 4 4 4zm0 2c-2.67 0-8 1.34-8 4v1c0 .55.45 1 1 1h14c.55 0 1-.45 1-1v-1c0-2.66-5.33-4-8-4z"
                      fill="#828282"
                    />
                  </svg>
                </span>
                <div class="invalid-feedback">{{ errors.subject }}</div>
              </div>
            </div>
            <div class="form-row">
              <div class="form-group">
                <label class="modal__label" for="class-content">Content</label>
                <quill-editor ref="myTextEditor" v-model="itemData.content" :options="editorOption"></quill-editor>
                <div class="invalid-feedback">{{ errors.content }}</div>
              </div>
            </div>
          </form>
        </div>
        <div class="modal-footer modal__footer">
          <button
            @click.prevent="clearErrors()"
            class="btn btn--md"
            type="button"
            data-dismiss="modal"
            aria-label="close"
            name="button"
          >Cancel</button>
          <button
            @click.prevent="sendEmail"
            class="btn btn--md btn__primary"
            type="button"
            v-bind="{'disabled': loading}"
            name="button"
          >Send Email</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import { quillEditor } from "vue-quill-editor";
import Loader from "../../components/Loader";
import form from "../../mixins/form";
import { sendEnail } from "../../utils/api/jobs";
import jobs from "../../utils/validations/job";
import { sendEmail } from "../../utils/api/jobs";
import { success, fail } from "../../utils/toast";

export default {
  name: "SendEmail",
  props: {
    emails: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  mixins: [form],
  data() {
    return {
      itemData: {},
      errors: {},
      loading: false,
      editorOption: {
        modules: {
          toolbar: [
            ["bold", "italic", "underline", "strike"],
            ["blockquote", "code-block"],
            [{ header: 1 }, { header: 2 }],
            [{ indent: "-1" }, { indent: "+1" }],
            [{ font: [] }],
            [{ direction: "rtl" }],
            [{ list: "ordered" }, { list: "bullet" }],
            [{ script: "sub" }, { script: "super" }],
            [{ header: [1, 2, 3, 4, 5, 6, false] }],
            [{ color: [] }, { background: [] }],
            [{ align: [] }],
            ["clean"],
            ["link", "image", "video"]
          ]
        }
      }
    };
  },
  computed: {
    editor() {
      return this.$refs.myTextEditor.quill;
    }
  },
  methods: {
    sendEmail() {
      this.itemData.recipient = [...this.emails];
      this.validate(this.itemData, jobs.email);
      if (Object.keys(this.issues).length) {
        this.errors = this.issues;
        return false;
      }
      this.loading = true;
      sendEmail(this.itemData)
        .then(response => {
          if (response.status === 200) {
            this.loading = false;
            this.itemData = {};
            success({
              title: "Success! Emails have been sent"
            });
          }
        })
        .catch(error => {
          this.loading = false;
          fail({
            title: "Oops! Something went wrong. Please try again."
          });
          return error;
        });
      return true;
    }
  },
  components: {
    Loader,
    quillEditor
  }
};
</script>
