import Joi from 'joi';

const job = {};

job.create = {
	title: Joi.string().min(2).max(24).required(),
	description: Joi.string().min(2).required(),
	type: Joi.any().valid(['full-time', 'contract']).required(),
	location: Joi.string().min(3).required(),
	closing_date: Joi.date().required(),
	career_id: Joi.number().required(),
	user_id: Joi.number().required()
};

job.update = {
	title: Joi.string().min(2).max(24),
	description: Joi.string().min(2),
	type: Joi.any().valid(['full-time', 'contract']),
	location: Joi.string().min(3),
	closing_date: Joi.date(),
	active: Joi.number(),
	id: Joi.number().min(1),
	career_id: Joi.number()
};
job.email = {
	subject: Joi.string().min(2).required(),
	content: Joi.required(),
	recipient: Joi.required()
}

export default job;
