import axios from 'axios';
import { isLoggedIn } from '../auth';

/**
 * Fetches the all user
 * @returns {Promise} user - a promise of user to be resolved.
 */
export default function getEmployers(limit, offset) {
	if (isLoggedIn()) {
		return axios.get(`/v1/employers?limit=${limit}&offset=${offset}`)
			.then(response => response);
	}
	return Promise.resolve(new Response({
		message: 'Unauthorised'
	}, {
		status: 403,
		statusText: 'Unauthorised'
	}));
}
